.navs-item{
  color: white;
  padding: 10px;
  font-size: 17px;
  font-weight: bold;
  position: relative; 
  padding: 20px; 
  transition: color 0.3s ease; 
  cursor: pointer; 
}

/* Remove text decoration for links */
.tab-link {
  text-decoration: none;
  color: inherit; /* Ensures text color matches the nav item color */
}

/* Hover and selected state color */
.navs-item.selected {
  color: #00BFFF; /* Change to your desired color */
}

/* Border line effect */
.navs-item::after {
  content: ''; /* Required to create a pseudo-element */
  position: absolute;
  left: 0; /* Align to the left */
  right: 0; /* Align to the right */
  bottom: 0; /* Position the border directly below the text */
  height: 2px; /* Adjust the height of the border */
  background-color: #00BFFF; /* Change to your desired border color */
  transform: scaleX(0); /* Start with no width */
  transition: transform 0.3s ease; /* Smooth transition for the border */
}

/* Expand the border on hover and for the selected item */
.navs-item.selected::after,
.navs-item:hover::after {
  transform: scaleX(1); /* Expand the border */
}


