    transition: background-color 0.3s ease;
    border: none;
    border-radius: 20px;
    color: #006666;
    font-weight: 500;
    text-align: center;
}

.project-tab.active {
    background-color: #00b2b2;
    color: black;
}

.project-tab:hover {
    background-color: #00b2b2;
    color: black;
}

.project-cards-container {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    grid-gap: 5px;
    gap: 5px;
    margin-top: 7%;
}

@media (min-width: 768px) {
    .project-cards-container {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (min-width: 1024px) {
    .project-cards-container {
        grid-template-columns: repeat(3, 1fr);
    }
}
.project-card {
    width: 100%;
    max-width: 400px;
    margin: 15px auto; /* Center card with top/bottom spacing */
    background-color: transparent;
    border: 1px solid cyan;
    border-radius: 10px;
    padding: 20px; /* Added padding for breathing space */
    text-align: center;
    overflow: hidden;
    transition: transform 0.3s ease, box-shadow 0.3s ease, background-color 0.3s ease;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.project-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.2);
    background-color: #191919;
}

.project-image {
    width: 100%;
    height: auto;
    border-radius: 8px;
    margin-bottom: 15px; /* Space below the image */
    transition: transform 0.3s ease;
}

.project-card:hover .project-image {
    transform: scale(1.05);
}

.project-title {
    font-size: 1.5rem; /* Title size */
    color: #5b5b5b; /* Dark title color */
    margin: 15px 0; /* Vertical spacing */
    text-align: center; /* Centered text */
    padding: 1px; /* Remove any padding */
    position: relative; /* For positioning if needed */
}


.project-description {
    font-size: 1rem;
    color: #767676;
    margin: 10px 0 15px; /* Space above and below description */
    line-height: 1.5; /* Line spacing for readability */
}

.project-skills {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    grid-gap: 8px;
    gap: 8px; /* Space between skill tags */
    margin-top: 15px;
    margin-bottom: 25px;
}

.project-skill {
    border: 1px solid #009999;
    color: #888888;
    padding: 5px 10px;
    border-top-right-radius: 10px;
    border-bottom-left-radius: 10px;
    font-size: 0.9rem;
    transition: background-color 0.3s ease, color 0.3s ease;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.project-skill:hover {
    border-color: #004c4c;
    background-color: #004c4c;
    color: #e5ffff;
}

.project-button {
    display: inline-block;
    padding: 10px 20px;
    background-color: #42b3c2;
    color: #032023;
    border-radius: 45%;
    text-decoration: none;
    transition: background-color 0.3s ease, transform 0.3s ease;
    font-weight: 500;
    margin-top: 15px; /* Space above the button */
}

.project-button:hover {
    background-color: #0d707d;
    transform: scale(1.1);
    color: #e5ffff;
    border-radius: 30%;
}

/* General Contact Section */
.contact-section {
    padding: 40px 20px;
    background-color: #000;
    color: #fff;
    text-align: center;
    width: 100%;
    min-height: 80vh;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.contact-text-container{
    position: relative;
    width: 100%;
    max-width: 800px;
    padding: 20px;
    align-self: flex-start;
    text-align: left;
}

.contact-title {
    color: cyan;
    font-family: 'sans-serif';
    font-size: 50px;
    margin-bottom: 2rem;
    text-shadow: 1px 1px 5px rgba(0, 255, 255, 0.5);
}

@media screen and (max-width:567px){
    .contact-title{
        text-align: center;
    }

}

.contact-content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 2rem;
    padding: 40px;
}

/* Social Icons */
.social-icons {
    display: flex;
    flex-direction: column;
    justify-content: center;
    grid-gap: 25px;
    gap: 25px; /* Reduced gap to minimize overflow */
    margin-bottom: 1.5rem;
    padding: 0; /* Remove padding to avoid overflow */
    overflow: hidden; /* Prevent overflow */
}

.social-icon {
    display: flex;
    align-items: center;
    text-decoration: none;
    color: cyan;
    font-size: 1.2rem;
    transition: color 0.3s ease, transform 0.3s ease, width 0.3s ease; /* Added width transition */
    width: 150px; /* Set a base width */
    padding-left: 10px;
}

.social-icon:hover {
    color: #71c6d1;
    transform: scale(1.1);
    width: 200px; /* Increased width on hover */
    z-index: 1; /* Bring hovered icon to the front */
    text-align: center;
}

.social-contact-icon {
    width: 30px;
    height: 30px;
    margin-right: 10px;
}


/* Contact Form */
.contact-form {
    width: 100%;
    max-width: 600px;
    background-color: #111;
    padding: 30px;
    border-radius: 8px;
    box-shadow: 0px 0px 20px rgba(0, 255, 255, 0.2);
    display: flex;
    flex-direction: column;
    grid-gap: 15px;
    gap: 15px;
}

.form-input {
    width: 100%;
    padding: 12px 15px;
    font-size: 1rem;
    background-color: #222;
    color: #fff;
    border: 1px solid cyan;
    border-radius: 5px;
    outline: none;
    transition: border-color 0.3s ease;
}

.form-input::placeholder {
    color: #888;
}

.form-input:focus {
    border-color: #00f7ff;
}

.send-button {
    background-color: cyan;
    color: #000;
    border: none;
    padding: 12px;
    font-size: 1.1rem;
    font-weight: bold;
    cursor: pointer;
    border-radius: 5px;
    transition: background-color 0.3s ease, color 0.3s ease;
    border-radius: 10px;
}

.send-button:hover {
    background-color: #095059;
    color: white;
}

/* Responsive Adjustments */
@media (max-width: 768px) {
    .contact-section{
        flex-direction: column;
        text-align: center;
    }

    .contact-title {
        font-size: 2.5rem;
    }

    .social-icons {
        flex-direction: column;
        grid-gap: 15px;
        gap: 15px;
    }

    .social-icon img {
        width: 28px;
        height: 28px;
    }

    .contact-form {
        width: 90%;
        padding: 15px;
    }

    .send-button {
        font-size: 1rem;
    }
}

@media (max-width: 480px) {
    .contact-section{
        flex-direction: column;
        text-align: center;
    }

    .contact-title {
        font-size: 2rem;
    }

    .form-input {
        font-size: 0.95rem;
        padding: 10px 12px;
    }
}

.response-container{
    background-color: #000;
    text-align: center;
    padding-top: 0px;
    padding-bottom: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
}

.response-message {
    color: cyan;
    margin-top: 1rem;
    font-size: 20px;
}

.done{
    color: cyan;
    font-size: 20px;
    margin-top: 20px;
    margin-left: 5px;
}

